.conteneur {
    display: flex;
    width: 90%;

}

.carte-home {
    flex: 1;
    margin-right: 15px;
    margin-left: 15px;
    height: 35vw;
    backdrop-filter: blur(5px);
    background-color: rgba(0, 0, 0, 0.337);
    border-radius: 15px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: rgba(255, 255, 255, 0.203) rgb(36, 36, 36);

    display: flex;
    justify-content: center;
    align-items: center;
}


.barre-horizontal-home {
    border: 1px solid #ffffff;
    margin-top: 1.2%;
    margin-bottom: 1.5%;
    width: 20%;
}


.title-home {
    font-family: "ITC";

    font-size: 3rem;
    font-weight: bold;
    color: white;
    text-align: center;
    transition: transform 0.3s ease, color 0.3s;
}

.carte-home:hover {
    cursor: pointer;
}

.carte-home:hover .title-home {
    position: sticky;
    font-weight: bold;
    color: orange;
    transform: scale(1.2);
}