.carte {
    width: 80%;
    height: 35vw;
    backdrop-filter: blur(5px);
    background-color: rgba(0, 0, 0, 0.337);
    border-radius: 15px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: rgba(255, 255, 255, 0.203) rgb(36, 36, 36);
}


.barre-horizontal {
    border: 1px solid #ffffff;
    margin-top: 1.2%;
    margin-bottom: 1.5%;
    width: 30%;
}

.contenu {
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.title {
    position: sticky;
    font-size: 2rem;
    font-weight: bold;
    color: white;
}

.paragraphe {
    text-align: justify;
    color: white;
}


.question {
    list-style: decimal;
    padding-bottom: 20px;
    font-size: 20px;

}

@keyframes scaleAnimation {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.3);
    }

    100% {
        transform: scale(1);
    }
}

.imageAnswer {
    color: white;
    font-size: 30px;
    vertical-align: -8px;
    animation: scaleAnimation 3s infinite;
}


.imageAnswer:hover {
    color: orange;
    cursor: pointer;
}