li {
    list-style: none;
}

a {
    text-decoration: none;
    color: white;
    font-size: 20px;
}

a:hover {
    color: orange;
}

.navbar {
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    backdrop-filter: blur(4px);
}

.navbar .logo a {
    padding-left: 1.5rem;
    font-size: 1.5rem;
    font-weight: bold;
}

.navbar .links {
    display: flex;
    gap: 2rem;
}

.navbar .links {
    display: flex;
    gap: 15rem;
    padding-right: 3rem;

}