.carte {
    width: 80%;
    height: 35vw;
    backdrop-filter: blur(5px);
    background-color: rgba(0, 0, 0, 0.337);
    border-radius: 15px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: rgba(255, 255, 255, 0.203) rgb(36, 36, 36);
}


.barre-horizontal {
    border: 1px solid #ffffff;
    margin-top: 1.2%;
    margin-bottom: 1.5%;
    width: 20%;
}

.contenu {
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.title {
    position: sticky;
    font-size: 2rem;
    font-weight: bold;
    color: white;
}

.content {
    text-align: center;
    color: white;
}

.listSchedule {
    list-style-type: none;
    padding: 0;
    margin: 0;

}

.listSchedule li {
    display: inline-block;
    margin-right: 30px;


}

.weekButton {
    border: 6px solid #ffffff;
    border-radius: 20px;
    padding-top: 10px;
    padding-left: 32.5px;
    padding-right: 32.5px;
    font-size: 80px;
    font-family: "ITC";

    padding-bottom: 10px;
}

.weekButton:hover {
    border: 6px solid orange;
    color: orange;
    cursor: pointer;
}


.weekButtonLarge {
    border: 6px solid #ffffff;
    border-radius: 20px;
    padding-top: 10px;
    padding-left: 8px;
    font-size: 80px;
    padding-right: 8px;
    padding-bottom: 10px;
    font-family: "ITC";

}

.weekButtonLarge:hover {
    border: 6px solid orange;
    color: orange;
    cursor: pointer;

}

.list {
    margin-top: 65px;
}